// src/components/trainer/TrainerList.jsx
import React from "react";
import { base } from "../../utils/classnames";
import TrainerCard from "./TrainerCard";
import clsx from "clsx"


export default function TrainerList({ trainers, onTrainerSelect }) {
  if (!trainers?.length) {
    return (
      <div className="text-center py-12">
        <p className="text-stone-500 dark:text-stone-400">
          No trainers available at the moment.
        </p>
      </div>
    );
  }

  return (
    <div className={clsx(base.grid.cards, "grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8")}>
      {trainers.map(trainer => (
        <TrainerCard
          key={trainer.id}
          trainer={trainer}
          onSelect={() => onTrainerSelect(trainer)}
        />
      ))}
    </div>
  );
}