import React, { useState } from "react";

export default function SessionDetails({ sessionDurations, onSessionDetailsChange }) {
  const [sessionData, setSessionData] = useState({
    technique: 'Shooting Technique',
    skillLevel: 'Beginner',
    notes: ''
  });

  const handleChange = (field, value) => {
    const updatedData = { ...sessionData, [field]: value };
    setSessionData(updatedData);

    if (onSessionDetailsChange) {
      onSessionDetailsChange(updatedData);
    }
  };

  return (
    <div className="bg-gray-50 text-gray-950 dark:bg-stone-900 dark:text-stone-100 rounded-lg p-4">
      <h4 className="font-medium mb-3">Session Details</h4>
      <div className="space-y-3">
        <div>
          <label
            htmlFor="focus-area"
            className="block text-sm font-medium mb-1 text-gray-700 dark:text-stone-100"
          >
            Focus Area
          </label>
          <select
            id="focus-area"
            className="w-full border-gray-600 bg-gray-100 dark:bg-stone-700 dark:text-stone-100 dark:border-gray-600 rounded-md px-3 py-2 border"
            value={sessionData.technique}
            onChange={(e) => handleChange('technique', e.target.value)}
          >
            <option value="Shooting Technique">Shooting Technique</option>
            <option value="Ball Handling">Ball Handling</option>
            <option value="Defense">Defense</option>
            <option value="Conditioning">Conditioning</option>
            <option value="Game Strategy">Game Strategy</option>
            <option value="General Skills">General Skills</option>
          </select>
        </div>

        <div>
          <label
            htmlFor="skill-level"
            className="block text-sm font-medium mb-1 text-gray-700 dark:text-stone-100"
          >
            Your Skill Level
          </label>
          <select
            id="skill-level"
            className="w-full border-gray-600 bg-gray-100 dark:bg-stone-700 dark:text-stone-100 dark:border-gray-600 rounded-md px-3 py-2 border"
            value={sessionData.skillLevel}
            onChange={(e) => handleChange('skillLevel', e.target.value)}
          >
            <option value="Beginner">Beginner</option>
            <option value="Intermediate">Intermediate</option>
            <option value="Advanced">Advanced</option>
            <option value="Elite">Elite</option>
          </select>
        </div>

        <div>
          <label
            htmlFor="notes"
            className="block text-sm font-medium mb-1 text-gray-700 dark:text-stone-100"
          >
            Notes (optional)
          </label>
          <textarea
            id="notes"
            rows="2"
            className="w-full border-gray-600 bg-gray-100 dark:bg-stone-700 dark:text-stone-100 dark:border-gray-600 rounded-md px-3 py-2 border resize-none h-20"
            placeholder="Any specific areas you'd like to work on..."
            value={sessionData.notes}
            onChange={(e) => handleChange('notes', e.target.value)}
          />
        </div>

        {sessionDurations && (
          <div className="text-sm text-gray-500 dark:text-gray-400">
            Session Duration: {sessionDurations.min}-{sessionDurations.max} minutes
          </div>
        )}
      </div>
    </div>
  );
}