// src/components/booking/TimeSlots.jsx
import React, { useState, useMemo } from "react";
import { parse, addMinutes, format, isBefore, isAfter, differenceInMinutes } from "date-fns";

export default function TimeSlots({
                                    coachAvailability,
                                    selectedDate,
                                    selectedTimes,
                                    onToggleTime,
                                    sessionDurations = { min: 30, max: 120 },
                                    selectedCoach,
                                    sessionDetails
                                  }) {
  const [selectedStartTime, setSelectedStartTime] = useState('');
  const [selectedEndTime, setSelectedEndTime] = useState('');

  if (!selectedDate || !coachAvailability) return null;

  const dayKey = format(selectedDate, "EEEE").toLowerCase();
  const ranges = Object.values(coachAvailability?.weeklyAvailability?.[dayKey] || {});
  const displayDate = format(selectedDate, "MMMM d, yyyy");

  // Generate available start times
  const availableStartTimes = useMemo(() => {
    const times = [];
    ranges.forEach(({ start, end }) => {
      if (!start || !end) return;
      try {
        let current = parse(start, "HH:mm", new Date());
        const endTime = parse(end, "HH:mm", new Date());

        while (isBefore(current, endTime)) {
          times.push({
            time: format(current, "HH:mm"),
            timeObj: new Date(current)
          });
          current = addMinutes(current, 15);
        }
      } catch (err) {
        console.error('Failed to parse time range:', { start, end }, err);
      }
    });

    return times.sort((a, b) => a.timeObj - b.timeObj);
  }, [ranges]);

  // Generate available end times based on start time
  const availableEndTimes = useMemo(() => {
    if (!selectedStartTime) return [];

    try {
      const startObj = parse(selectedStartTime, "HH:mm", new Date());
      const times = [];

      // Add 30min and 45min options
      const thirtyMin = addMinutes(startObj, 30);
      const fortyFiveMin = addMinutes(startObj, 45);

      times.push({
        time: format(thirtyMin, "HH:mm"),
        duration: 30,
        label: `${format(thirtyMin, "h:mm a")} (30min)`
      });

      times.push({
        time: format(fortyFiveMin, "HH:mm"),
        duration: 45,
        label: `${format(fortyFiveMin, "h:mm a")} (45min)`
      });

      return times;
    } catch (err) {
      console.error('Failed to generate end times:', err);
      return [];
    }
  }, [selectedStartTime]);

  const formatTime = (timeStr) => {
    try {
      return format(parse(timeStr, "HH:mm", new Date()), "h:mm a");
    } catch {
      return timeStr;
    }
  };

  const handleAddSession = () => {
    if (selectedStartTime && selectedEndTime) {
      const endTimeData = availableEndTimes.find(t => t.time === selectedEndTime);
      const duration = endTimeData?.duration || 30;

      onToggleTime?.(selectedStartTime, duration, {
        coach: selectedCoach,
        technique: sessionDetails?.technique || "Training Session",
        skillLevel: sessionDetails?.skillLevel || "Intermediate"
      });

      setSelectedStartTime('');
      setSelectedEndTime('');
    }
  };

  if (availableStartTimes.length === 0) {
    return (
      <div className="dark:bg-stone-900 dark:text-stone-100 p-4">
        <h3 className="font-medium mb-2">Book Training Session</h3>
        <p className="text-sm text-gray-400 mb-2">{displayDate}</p>
        <p className="text-stone-500 dark:text-stone-400 text-center py-8">
          No available times for {displayDate}
        </p>
      </div>
    );
  }

  return (
    <div className="dark:bg-stone-900 dark:text-stone-100 p-4">
      <h3 className="font-medium mb-2">Book Training Session</h3>
      <p className="text-sm text-gray-400 mb-2">{displayDate}</p>

      <div className="space-y-4">
        <div>
          <label className="block text-sm font-medium mb-2 text-gray-300">Start Time</label>
          <select
            className="w-full bg-gray-100 dark:bg-stone-700 border border-gray-600 text-gray-800 dark:text-stone-100 rounded px-3 py-2 text-sm"
            value={selectedStartTime}
            onChange={(e) => {
              setSelectedStartTime(e.target.value);
              setSelectedEndTime(''); // Reset end time when start time changes
            }}
          >
            <option value="">Select start time...</option>
            {availableStartTimes.map(({ time }) => (
              <option key={time} value={time}>
                {formatTime(time)}
              </option>
            ))}
          </select>
        </div>

        {selectedStartTime && (
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-300">End Time</label>
            <select
              className="w-full bg-gray-100 dark:bg-stone-700 border border-gray-600 text-gray-800 dark:text-stone-100 rounded px-3 py-2 text-sm"
              value={selectedEndTime}
              onChange={(e) => setSelectedEndTime(e.target.value)}
            >
              <option value="">Select end time...</option>
              {availableEndTimes.map(({ time, label }) => (
                <option key={time} value={time}>
                  {label}
                </option>
              ))}
            </select>
          </div>
        )}

        {selectedStartTime && selectedEndTime && (
          <div className="bg-gray-800 rounded-lg p-3 border border-gray-600">
            <div className="text-sm">
              <div className="font-medium text-white mb-2">Session Preview</div>
              <div className="text-gray-300 mb-1">
                <span className="font-medium">Trainer:</span> {selectedCoach?.name}
              </div>
              <div className="text-gray-300 mb-1">
                <span className="font-medium">Focus:</span> {sessionDetails?.technique || "Training Session"}
              </div>
              <div className="text-gray-300 mb-1">
                <span className="font-medium">Level:</span> {sessionDetails?.skillLevel || "Beginner"}
              </div>
              <div className="text-gray-300 mb-1">
                <span className="font-medium">Time:</span> {formatTime(selectedStartTime)} - {formatTime(selectedEndTime)}
              </div>
              <div className="text-gray-300">
                <span className="font-medium">Duration:</span> {availableEndTimes.find(t => t.time === selectedEndTime)?.duration || 30}min
              </div>
            </div>
            <button
              onClick={handleAddSession}
              className="w-full mt-3 bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded transition"
            >
              Add This Session
            </button>
          </div>
        )}
      </div>
    </div>
  );
}